import React from 'react'
import { useContext } from 'react';
import { useRef } from 'react';
import Context from '../Context/Context';
import './changer.css';
import { changeUsers } from './changeUsers';
import { useDispatch } from 'react-redux';
import { changePosts } from './changePosts';


function Changer() {
    let dispatch = useDispatch()
    let name = useRef()
    const {usersDate, postsDate, currentUser, currentPerson} = useContext(Context)

    async function ChangeGets(){
        await changePosts(postsDate, currentUser, name.current.value, setP)
        await changeUsers(usersDate, currentUser, currentPerson,  name.current.value, setU)
        

    }

    function setU(arg, cp, cu){
        dispatch({type: 'SET_UD', item: arg})
        dispatch({type: 'SET_C_P', item: cp})
        dispatch({type: 'SET_C_U', item: cu})
        
    }

    function setP(arg, cu){
        dispatch({type: 'SET_P', item: arg})
        dispatch({type: 'SET_C_U', item: cu})
        
    }
  return (
    <div className='blocke'>
        <input ref={name} type="text" />
        <div className="button_bl" onClick={() => ChangeGets()} >Change</div>
    </div>
  )
}

export default Changer